import { Component } from '@angular/core';

import { AlertController } from 'ionic-angular';


@Component({
  selector: 'page-pediatricCalcuPage',
  templateUrl: 'pediatricCalcuPage.html'
})
export class pediatricCalcuPage {
  result: any;
  a:number;
  b:number;

  x:number;
  y:number;

  constructor(public alertCtrl: AlertController) {
  }

  calculation1(f){
    this.a = f.controls['a'].value;
    this.b = f.controls['b'].value;
    this.result = (this.a / ((this.a*1) + 12)) * this.b;
    console.log(this.result);
    this.showAlert();
  }

  calculation2(f){
    this.x = f.controls['x'].value;
    this.y = f.controls['y'].value;
    this.result = (this.x / 150) * this.y ;
    console.log(this.result);
    this.showAlert();
  }
  showAlert() {
    const alert = this.alertCtrl.create({
      title: 'Dosage for the kid is',
      subTitle: this.result,
      buttons: ['OK']
    });
    alert.present();
  }




}



