import { Component } from '@angular/core';

import { AlertController } from 'ionic-angular';

@Component({
  selector: 'page-kineticCalcuPage',
  templateUrl: 'kineticCalcuPage.html'
})
export class kineticCalcuPage {

  result: any;
  a:number;
  b:number;
  c:number;
  d:number;
  x:number;
  y:number;
  constructor(public alertCtrl: AlertController) {
    }

  calculation1(f){
    this.a = f.controls['a'].value;
    this.b = f.controls['b'].value;
    this.c = f.controls['c'].value;
    this.d = f.controls['d'].value;
    this.result = (this.a / this.b) * (this.c / this.d) * 100;
    console.log(this.result);
    this.showAlert();
  }

  calculation2(f){
    this.x = f.controls['x'].value;
    this.y = f.controls['y'].value;
    this.result = (this.x / this.y) ;
    console.log(this.result);
    this.showAlert();
  }
  showAlert() {
    const alert = this.alertCtrl.create({
      title: 'your Answer!',
      subTitle: this.result,
      buttons: ['OK']
    });
    alert.present();
  }

}


